import React, { Component } from 'react';
import GoogleMapReact from 'google-map-react';
import marker from '../images/marker.png';

const Location = () => <div style={{
    color: 'white',
    padding: '15px 10px',
    display: 'inline-flex',
    textAlign: 'center',
    alignItems: 'center',
    justifyContent: 'center',
    transform: 'translate(-50%, -50%)'
  }}><img src= {marker} className= 'marker'/></div>;

class Map extends Component {
  static defaultProps = {
    center: {
      lat: 59.95,
      lng: 30.33
    },
    zoom: 14
  };

  render() {
    return (
      <div style={{ height: '75vh', width: '100%' }}>
        <GoogleMapReact
          bootstrapURLKeys={{ key: 'AIzaSyC2KzWLHrIUqZII1b_t7bhJmLVk3SNOTTQ' }}
          defaultCenter={this.props.center}
          defaultZoom={this.props.zoom}>

          <Location
           lat={59.955413}
           lng={30.337844}/>

          </GoogleMapReact>
      </div>
    );
  }
}

export default Map;
