import React, {Component} from 'react';
import PropsTypes from 'prop-types';

class ResizableMenu extends Component {


static propsTypes = {
  menuOpen: PropsTypes.bool.isRequired
}

state= {
  menuOpen: false
}

componentWillReceiveProps(update){
  this.setState({menuOpen: update.menu});
}

render(){
if(this.state.menuOpen){
  return(
    <div className='resizable-menu-container'>
      <ul className= 'resizable-menu'>
        <li className= 'nav-item'><a href='#'>Menu</a></li>
        <li className= 'nav-item'><a href="#">Parties</a></li>
        <li className= 'nav-item'><a href="#">Gallery</a></li>
        <li className= 'nav-item'><a href="#">Contact</a></li>
        <li className= 'nav-item'><a href="#">Order</a></li>
      </ul>
    </div>
    );
  }else {
    return(<div></div>);
  }

  }
}


export default ResizableMenu;
