import React, {Component} from 'react';

class Field extends Component {

  constructor(props){
    super(props)

    this.state = {
      fields: {
        firstname: 'FirstName*',
        lastname: 'LastName*',
        email: 'Email',
        phone: 'Mobile Number',
        address: 'Address',
        address_two: 'Address (Line Two)',
        zipCode: 'Zip Code*',
      },

      contactFields: {
        fullname: 'Your Full Name (Required)',
        email: 'Your Email (Required)',
        comment: 'Message (Required)'
      },

      partiesFields: {
        name: '',
        phone: '',
        date: '',
        occasion: '',
        comment: '',
        lastname: '',
        email: '',
        time: '',
        guesthouse: '',
      }
    }

  }

  onSelectChange = (evt)=>{

  }

  componentWillReceiveProps(update){
    this.setState ({currentPage: update.currentPage})
  }

  partiesFields = () => (
    <div className = 'field content'>
      <div>
          <div>
            <label> Name * </label>
            <input type='input' name = 'fullname'className ='field text' value= {this.state.partiesFields.name} />
          </div>
          <div>
            <label> Phone * </label>
            <input type='input' name = 'email' className ='field text' value={this.state.partiesFields.phone}/>
          </div>

          <div>
            <label> Date * </label>
            <input type='input' name = 'email' className ='field text' value={this.state.partiesFields.occasion}/>
          </div>

          <div>
            <label> Occasion * </label>
            <input type='input' name = 'email' className ='field text' value={this.state.partiesFields.comment}/>
          </div>
      </div>

      <div>
          <div>
            <label> Last Name * </label>
            <input type='input' name = 'fullname'className ='field text' value= {this.state.partiesFields.lastname} />
          </div>
          <div>
            <label> Email Address * </label>
            <input type='input' name = 'email' className ='field text' value={this.state.partiesFields.email}/>
          </div>

          <div>
            <label> Time * </label>
            <input type='input' name = 'email' className ='field text' value={this.state.partiesFields.time}/>
          </div>

          <div>
            <label> Guest Count * </label>
            <input type='input' name = 'email' className ='field text' value={this.state.partiesFields.guesthouse}/>
          </div>
      </div>

      <div className= 'parties-comment'>
        <div>
          <label> Comments * </label>
          <textarea className = 'field area' value= {this.state.contactFields.comment}/>
        </div>
      </div>
      <div className = 'submit'>Submit</div>
    </div>
  );


  contactFields = () => (
    <div className = 'field content'>
      <label> Name * </label>
      <input type='input' name = 'fullname'className ='field text' value= {this.state.contactFields.fullname} />
      <label> Email * </label>
      <input type='input' name = 'email' className ='field text' value={this.state.contactFields.email}/>
      <label> Comments * </label>
      <textarea className = 'field area' value= {this.state.contactFields.comment}/>
      <div className = 'submit'>Submit</div>
    </div>
  );

  homeFields = () => (
  <div className = 'field content'>
    <input type='input' name = 'firstname'className ='field text' value= {this.state.fields.firstname} />
    <input type='input' name = 'lastname' className ='field text' value={this.state.fields.lastname}/>
    <input type='input' name = 'email' className ='field text' value={this.state.fields.email}/>
    <input type='input' name = 'phone' className ='field text' value={this.state.fields.phone}/>
    <select id='birth month' name='birth_month' className ='field text' onChange={this.onSelectChange}>
     <option value="birthmonth">Select Birth Month*</option>
   </select>
   <select id='birth day' name='birth_day' className ='field text' onChange={this.onSelectChange}>
     <option value="birthday">Select Birth Day*</option>
   </select>
    <input type='input' name = 'address' className ='field text' value={this.state.fields.address}/>
    <input type='input' name = 'address_two' className ='field text' value={this.state.fields.address_two}/>
    <input type='input' name = 'zipcode' className ='field text' value={this.state.fields.zipCode}/>
    <div className = 'submit'>Submit</div>
  </div>
  );

  render(){
    if(this.props.isContactField){ //create attribute in the contact
      return(
        <div>
          {this.contactFields()}
        </div>
      );
    }else if(this.props.isPartiesField){
      return(
        <div>
          {this.partiesFields()}
        </div>
      );
    }else {
      return(
        <div>
          {this.homeFields()}
        </div>
      );
    }
  }
}

export default Field;
