import React, { Component } from 'react';
import NavMenu from '../src/component/NavMenu';
import ResizableMenu from '../src/component/ResizableMenu';
import Footer from '../src/component/Footer';
import Address from '../src/component/Address';
import {BrowserRouter, Route, Switch} from 'react-router-dom';
import Home from '../src/component/Home';
import Contact from '../src/component/Contact';
import Parties from '../src/component/Parties';


class App extends Component {

  constructor(props){
    super(props);

    this.state = {
      navMenu: {
        status: false,
        activeItem: 'home'
      },
      background: {
        title: '',
        summary: ''
      },
    }

    this.handleNavClick = this.handleNavClick.bind(this);
    this.handleMenuClick = this.handleMenuClick.bind(this);

  }

  componentDidMount(){
    this.setState({navMenu: Object.assign({}, this.state.navMenu, {status: false})});
  }

  handleNavClick(evt){
    this.setState({navMenu: Object.assign({},
    this.state.navMenu, {status: this.state.navMenu.status ? false : true })});
  }

  handleMenuClick(item){
    this.setState({navMenu: Object.assign({}, this.state.navMenu, {activeItem: item })});
  }

  render() {
    return (
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <div className = 'main-container'>
          <ResizableMenu menu={this.state.navMenu.status}/>
          <NavMenu onNavClick = {this.handleNavClick} onMenuItem = {this.handleMenuClick}/>
            <div className='main-content'>

            <Switch>
              <Route exact path ='/' component = {Home} />
              <Route path = '/parties' component = {Parties} />
              <Route path = '/contact' component = {Contact}/>
            </Switch>
            </div>
          <Address/>
          <Footer/>
        </div>
    </BrowserRouter>
    );
  }
}

export default App;
