import React, {Component} from 'react';
import roseLogo from '../images/roseLogo.png';
import ordernow from '../images/ordernow.jpg'
import {NavLink} from 'react-router-dom';
import '../App.css';

class NavMenu extends Component {

  handleToggleClick = (evt) =>{
    const navToggle = document.querySelector('#toggle');
    navToggle.classList.toggle('on');
    this.props.onNavClick(evt);
  }

  handleMenuItemClick = (evt) => {
    this.props.onMenuItem(evt.currentTarget.dataset.id)
  }

  render(){
    return(
      <header role= 'banner'>
        <NavLink to ="/"><img className='header-logo' src={roseLogo} alt="logo"/></NavLink>
        <nav className= 'drawer' id='nav'>
          <ul className= 'nav-menu'>
            <li className= 'nav-item' onClick = {this.handleMenuItemClick} data-id = 'menu'><NavLink to='#'>Menu</NavLink></li>
            <li className= 'nav-item' onClick = {this.handleMenuItemClick} data-id = 'parties'><NavLink to="/parties">Parties</NavLink></li>
            <li className= 'nav-item' onClick = {this.handleMenuItemClick} data-id = 'gallery'><NavLink to="#">Gallery</NavLink></li>
            <li className= 'nav-item' onClick = {this.handleMenuItemClick} data-id = 'contact'><NavLink to="/contact">Contact</NavLink></li>
          </ul>
        </nav>
        <div className='btn-order'><img src={ordernow}/></div>
        <div id='toggle' onClick={this.handleToggleClick}>
          <div className='span' id='bar1'></div>
          <div className='span' id='bar2'></div>
          <div className='span' id='bar3'></div>
        </div>
    </header>
    );
  }
}

export default NavMenu;
